// vuex 文章列表 模块
import axios from '../../plugins/axios'
export default {
    state: () => ({
        list: [],
        loading:false,
        iaAll:false
    }),
    mutations: {
        getPost(state, res) {
            state.list = res
            // console.log(res);
        },
        loadMorePost(state, res) {
            state.list = [...state.list, ...res]
        }

    },
    actions: {
        // 请求文章列表 有两种情况
        // 1.初始请求
        // 2.加载更多请求
        // 依据传参带不带page 属性，不带就是初始请求，反之就是加载更多

        // 获取文章列表数据
        async getPost({ commit ,state}, arg) {
            state.loading = true
            const { page, tab } = arg
            const searchStr = page ? `?tab=${tab}&page=${page}&limit=20` : `?tab=${tab}`
            const res = await axios.get(`/topics${searchStr}`)
            state.loading = false
            page ? commit('loadMorePost', res.data) : commit('getPost', res.data)

        }
    }
}