<template>
  <div class="home">
    <Panel class="home-left">
      <template #header>
        <router-link
          :class="['tab', { active: tab === '全部' && $route.path === '/' }]"
          :to="tab | transformTab"
          v-for="tab in tabs"
          :key="tab"
          active-class="active"
        >
          <el-button :disabled="loading" type="primary" plain size="mini">{{
            tab
          }}</el-button>
        </router-link>
      </template>
      <template #content>
        <router-view></router-view>
      </template>
    </Panel>
    <Panel v-if="token" class="home-right">
      <template #header> 个人信息 </template>
      <template #content>
        <p>欢迎 {{ user.loginname }}</p>
        <router-link to="/topic/create">
          <el-button size="small" type="success"> 发布话题</el-button>
        </router-link>
      </template>
    </Panel>
    <Panel v-else class="home-right">
      <template #header> 未登录 </template>
      <template #content>
        <p>您还没登录，请登录</p>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel },
  data() {
    return {
      tabs: ["全部", "精华", "分享", "问答", "招聘", "客服端测试"],
    };
  },
  computed: {
    ...mapState({
      loading: (state) => state.postList.loading,
      user: (state) => state.login.info,
    }),
    token() {
      return localStorage.getItem("token");
    },
  },
};
</script>

<style lang='less'>
.home {
  display: flex;
  .home-left {
    margin-right: 20px;
    width: 75%;
    min-width: 800px;
  }
  .home-right {
    width: 20%;
    min-width: 200px;
    .panel-content {
      min-height: 150px;
    }
  }
  .tab {
    display: inline-block;
    padding: 2px 4px;
    color: green;
    border-radius: 3px;
    font-size: 14px;
  }
  .tab + .tab {
    margin-left: 10px;
  }
  .active .el-button--primary.is-plain {
    background-color: #409eff;
    color: #fff;
  }
}
</style>