//vuex  个人信息页
import axios from '../../plugins/axios' 
export default {
    state: ()=>({
        // 对象的初始值一般写成null 不写成 {}
        userInfo:null
    }),
    mutations: {
        getUserInfo(state,res){
            state.userInfo = res
            // console.log(res);
        }
    },
    actions: {
        // 获取文章列表数据
        async getUserInfo({commit},user){
            const res = await axios.get(`/user/${user}`)
            // console.log(res.data);
            commit('getUserInfo',res.data)
        }
    }
  }