// 获取未读消息和已读消息信息 数量
import axios from "../../plugins/axios";
export default {
    state:()=>({
        has_read_messages:[],
        hasnot_read_messages:[],
        noread_count:0
    }),
    mutations:{
        getMessage(state,res){
            state.has_read_messages = res.has_read_messages
            state.hasnot_read_messages = res.hasnot_read_messages
        },
        getMessageCount(state,res){
            state.noread_count = res.data
        }
    },
    actions:{
        async getMessage({commit}){
            const token = localStorage.getItem('token')
            const res = await axios.get(`messages?accesstoken=${token}`)
            commit('getMessage',res.data)
        },
        async allRead(){
            const token = localStorage.getItem('token')
             await axios.post('/message/mark_all',{accesstoken:token})
        },
        async getMessageCount({commit}){
            const token = localStorage.getItem('token')
            const res = await axios.get(`message/count?accesstoken=${token}`)
            commit('getMessageCount',res)
        }
    }
}
