//用户登录模块

import axios from '../../plugins/axios'
export default {
    state: () => ({
        info: null
    }),
    mutations: {
        getInfo(state, res) {
            state.info = res
        }
    },
    actions: {
        async getInfo({ commit }, token) {
            const res = await axios.post('/accesstoken', { accesstoken: token })
            localStorage.setItem('info',JSON.stringify(res))
            localStorage.setItem('token',token)
            // console.log(res);
            commit('getInfo', res)
        },
    }
}