<template>
  <header>
    <div class="header-inner">
      <h1>
        <router-link to="/">
          <img src="../assets/logo.png" alt="" />
        </router-link>
      </h1>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <div class="avator" v-if="info">
          <el-avatar
            shape="square"
            size="medium"
            :src="info.avatar_url"
          ></el-avatar>
          <a href="#" @click.prevent="logOut">退出</a>
          <!-- <el-badge :value="count" class="item" type="primary">
            未读消息
          </el-badge> -->
          <router-link to="/message"
            >未读消息
            <i v-if="count" class="iconfont icon-weiduxiaoxi">{{ count }}</i>
          </router-link>
      
        </div>
        <router-link v-else to="/login">登录</router-link>
      </div>
    </div>
  </header>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
export default {
  computed: {
    ...mapState(["login"]),
    ...mapState({
      count: (state) => state.message.noread_count,
    }),
    info() {
      return this.login.info;
    },
  },
  created() {
    const token = localStorage.getItem("token");
    if (token) {
      this.getMessageCount();
    }
  },
  methods: {
    ...mapActions(["getMessageCount"]),
    ...mapMutations(["getInfo"]),
    logOut() {
      // this.getInfo(null);

      this.$router.push("/login");
      this.getInfo(null);
      localStorage.removeItem("info");
      localStorage.removeItem("token");
      this.$message({
        message: "已退出",
        type: "success",
      });
    },
  },
};
</script>

<style lang='less'>
header {
  background-color: rgb(54, 57, 107);
  .header-inner {
    width: 90%;
    height: 50px;
    max-width: 1400px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    h1 {
      width: 120px;
      margin: 0;
      img {
        margin-top: 10px;
        width: 100%;
      }
    }
    .header-right {
      display: flex;
      align-items: center;
      a {
        color: #fff;
        margin-left: 20px;
        font-size: 14px;
      }
      .avator {
        display: flex;
        align-items: center;
        margin-left: 20px;
      }
    }
  }
}
</style>