// vuex title 文章列表 模块
import axios from '../../plugins/axios'
export default {
    state: () => ({
        // 对象的初始值一般写成null 不写成 {}
        Topic: null
    }),
    mutations: {
        getTopicPost(state, res) {
            state.Topic = res
        },
        changeCollect(state, bool) {
            state.Topic.is_collect = bool
        },
        // action 是点赞行为 值是 up 就是点赞 反之是  down 取消点赞
        // id 是评论的id
        changeUps(state, { id, action }) {
            // bool 是true 或者
            const userId = JSON.parse(localStorage.getItem('info')).id
            const currenReply = state.Topic.replies.find(ele => ele.id === id)
            if (action === 'up') {
                currenReply.is_uped = true
                currenReply.ups.push(userId)
            } else {
                currenReply.is_uped = false
                currenReply.ups = currenReply.ups.filter(ele => ele !== userId)
            }

            // 除了要修改 is_uped 之外 点赞的个数也要变化，就是要修改ups ups里存的是点赞用户的id
            // 点赞时 需要将当前用户的id push到 ups的数组内 反之需要删除

        }
    },
    actions: {
        // 获取文章列表数据
        async getTopicPost({ commit }, id) {
            // 文章详情 两种请求
            // 当登录之后再去请求文章详情的话，需要带 assesstoken参数
            const token = localStorage.getItem('token')
            const url = token ? `/topic/${id}?accesstoken=${token}` : `/topic/${id}`
            const res = await axios.get(url)
            commit('getTopicPost', res.data)
        },
        //收藏 或者取消收藏
        // id 是文章id
        // is_collect 是 true 就是收藏请求 是false 是取消请求
        // post /topic_collect/collect 收藏
        // post /topic_collect/de_collect 取消
        async collectHandle({ commit }, { id, is_collect }) {
            const token = localStorage.getItem('token')
            if (is_collect) {
                // 收藏主题
                await axios.post('/topic_collect/collect', { topic_id: id, accesstoken: token })
                commit('changeCollect', true)
            } else {
                // 取消主题
                await axios.post('/topic_collect/de_collect', { topic_id: id, accesstoken: token })
                commit('changeCollect', false)
            }
        },
        // post /reply/:reply_id/ups 为评论点赞
        async upsHandle({ commit }, id) {
            const token = localStorage.getItem('token')
            const res = await axios.post(`/reply/${id}/ups`, { accesstoken: token })
            commit('changeUps', { id:id, action: res.action })
        },
        // post /topic/:topic_id/replies 新建评论
        async addComment({dispath},{id,content}){
            const token = localStorage.getItem('token')
            const res = axios.post(`/topic/${id}/replies`,{accesstoken:token,content:content})
            // 请求的返回值{success: true, reply_id: '5433d5e4e737cbe96dcef312'}
            // 请求成功了 后台更新了
            // 其实前端只需要展示出来添加了 但是有的时候信息较少 无法添加
            // 我们可以重新发 getpost请求 将整个文章详情重新请求 那么新增的评论也就更新了
            dispath('getTopicPost',id)
        }
    }
}