import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
Vue.use(VueRouter)

const routes = [
  {
    path:'/login',
    name:'login',
    component:()=>import('../views/Login.vue')
  },
  {
    path:'/message',
    name:'message',
    component:()=>import('../views/Message.vue')
  },
  // 列表访问 /xx 去404
  // 穷PostList页面里判断watch里面 如果tab不是正确的 转到 404页面
  {
    path:'/err',
    name:'err',
    component:()=>import('../views/Err.vue')
  },
  {
    path:'/topic/create',
    name:'create',
    component:()=>import('../views/CreatPost.vue')
  },
  {
    path:'/topic/:id',
    name:'titlepage',
    component:() => import('../views/TitlePage.vue')
    // props:true
  },
  {
    path:'/user/:user',
    name:'user',
    component:()=>import('../views/User.vue')
  },
  {
    path:'/user/:user/topics',
    name:'topics',
    component:()=>import('../views/MoreTitle.vue')
  },
  // 首页一般不懒加载
  {
    path: '/',
    component: Home,
    children: [
      {
        path: '',
        name: 'home',
        component: () => import('../views/PostList.vue')
      },
      {
        path: ':tab',
        component: () => import('../views/PostList.vue')
      }
    ]
  },
  // 404组件 所有的
  {
    path:'*',
    component:()=>import('../views/Err.vue')
  }
 
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
